.info-user {
  width: 300px;
  background-color: rgb(240, 240, 243);
  border-radius: 10px;
  overflow: hidden;
  margin: auto;
  padding: 10px 20px;
  border-spacing: 10px;
  border-bottom: 2px solid rgb(35, 48, 160);
  border-right: 2px solid rgb(35, 58, 160);
}
.info-user img {
  border-radius: 50%;
  height: 70px;
  width: 70px;
}
.info-user h4 {
  color: aliceblue;
  background-color: rgb(35, 58, 160);
  padding: 0px 10px;
}
